import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    NavbarText,
    Container, Row, Col,
    Card, Button, CardHeader, CardFooter, CardBody,
    CardTitle, CardText,
    Input, Form, FormGroup, Label
  } from 'reactstrap';
  import 'bootstrap/dist/css/bootstrap.min.css';
  import React, { useState } from 'react';
  import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
  import 'font-awesome/css/font-awesome.min.css';
  
  import { faPlusSquare } from '@fortawesome/free-solid-svg-icons'
  import { faTrashAlt } from '@fortawesome/free-solid-svg-icons'
  function MultiSelect(props) {
  
    const [question, setQuestion] = useState({
        list : ["", ""],
        name : "",
        type : "multi"
    });

    const addQuestion = () => {
        const newQuestion = Object.assign({}, question)
        newQuestion.list.push("")
        setQuestion(newQuestion)
    }

    
 

  
    
    return (
      <div>
          <Card>
            <CardHeader>{props.number}. Multi Select
            <a onClick={()=>{props.removeQuestion(props.number-1)}} href="#"><FontAwesomeIcon icon={faTrashAlt} color="red" className="float-right" size="lg"/></a>
            </CardHeader>
            <CardBody>
            <CardTitle tag="h5">
              <FormGroup row>
              <Label for="name" sm={2}>Question name:</Label>
                  <Col sm={10}>
                  <Input required type="text" name={`questions[${props.number-1}][name]`} id="name" placeholder="with a placeholder"  />
                  </Col>
              </FormGroup>
            </CardTitle>
            <CardText>
            <input type="hidden" name={`questions[${props.number-1}][type]`} value="multi"/> 
            
            <hr></hr>    
            {question.list.map((value, index) => {
              return <FormGroup row>
                        <Label for="name" sm={2}>Answer {index+1}:</Label>
                            <Col sm={10}>
                            <Input required type="text" name={`questions[${props.number-1}][options][${index+1}]`} id="name" placeholder="with a placeholder" />
                            </Col>
                    </FormGroup>
            })}
            <a href='javascript:void(0)' onClick={()=> addQuestion()}>Add answer <FontAwesomeIcon icon={faPlusSquare} /></a>

            </CardText>
            </CardBody>
        </Card>
      <br></br>
      </div>
    );
  }
  
  export default MultiSelect;
  